import React from 'react';
import './MarketplaceSlider.scss';
import Slider from 'react-slick';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import leftArr from '../../Assets/home/arrow_left.svg'
import rightArr from '../../Assets/home/arrow_right.svg'

function SampleNextArrow(props) {
  const { className, style, onClick } = props;
  return (
    <button
      type="button"
      data-role="none"
      className="slick-arrow slick-next"
      onClick={onClick}
      style={{backgroundImage: `url(${rightArr})` }}
    >
    </button>
  );
}

function SamplePrevArrow(props) {
  const { className, style, onClick } = props;
  return (
    <button
      type="button"
      data-role="none"
      className="slick-arrow slick-prev"
      onClick={onClick}
      style={{...style, backgroundImage: `url(${leftArr}), no-repeat contain` }}
    >
    </button>
  );
}

const MarketplaceSlider = (props) => {
  
  const sliderInnerBlocks = props.nftSlidesImgs.map((el, index) => {
    return(
      <props.component key={index} arr={el} darkMode={props.darkMode} />
    )
  })

  const { title } = props;
  const settings = {
    dots: false,
    infinite: true,
    slidesToShow: props.slidesToShow,
    slidesToScroll: 1,
    rows: props.slidesPerRow,
    slidesPerRow: 1,
    arrows: true,
    autoplay: false,
    pauseOnHover: true,
    nextArrow: <SampleNextArrow />,
    prevArrow: <SamplePrevArrow />,
    responsive: [
      {
        breakpoint: 991,
      },
      {
        breakpoint: 767,
        settings: {
          dots: true
        }
      },
      {
        breakpoint: 574,
        settings: {
          dots: true,
        }
      }
    ]
  };

  return (
    <div className={props.darkMode ? "marketplace_slider dark" : "marketplace_slider"}>
      <div className="container">
        <div>
          <Slider {...settings}>
            {sliderInnerBlocks}
          </Slider>
        </div>
      </div>
    </div>
  );
};

export default MarketplaceSlider;
