import React from 'react';
import './CollectionBlock.scss';
import Heart from '../../Assets/home/heart.svg'

const CollectionBlock = (props) => {
  return (
    <div className={props.darkMode ? "collection_block_wrapper dark" : "collection_block_wrapper"}>
     <div className="collection_block">
      <div className="collection_block_img">
        <div className="collection_block_img_wrapper ">
          <img src={props.arr.img} alt="collection block img" />
        </div>
      </div>
      <div className="author">
        <img src={props.arr.author} alt="author" />
      </div>
      <div className="collection_block_info">
        <h6>{props.arr.value}</h6>
        <a>$345,000</a>
        <div className="collection_block_footer">
          <span>
            <img src={Heart} alt="heart" />
            1,2 k
          </span>
          <span className="count">1000 items</span>
        </div>
      </div>
    </div>
  </div>
  );
};

export default CollectionBlock;
